/**dashboard**/
.semi-circle {
    width: 160px;
    height: 160px;
    border: 12px solid #E2E2E2;
    border-radius: 50%;
    overflow: hidden;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    margin: 20px;
}

.border_orange {
    width: 200%;
    background: #F5852E;
    animation: myfirst 10s infinite;
    -moz-animation: myfirst 10s infinite; /* Firefox */
    -webkit-animation: myfirst 10s infinite; /* Safari and Chrome */
    -o-animation: myfirst 10s infinite; /* Opera */
}

.border_blue {
    width: 200%;
    background: #00B6CD;
    animation: myfirst 15s infinite;
    -moz-animation: myfirst 15s infinite; /* Firefox */
    -webkit-animation: myfirst 15s infinite; /* Safari and Chrome */
    -o-animation: myfirst 15s infinite; /* Opera */
}

@keyframes myfirst {
    0% {
        height: 0;
    }
    50% {
        height: 65%;
    }
    100% {
        height: 0;
    }
}

@-webkit-keyframes myfirst { /* Safari 与 Chrome */
    0% {
        height: 0;
    }
    50% {
        height: 65%;
    }
    100% {
        height: 0;
    }
}

