@charset "UTF-8";
html,body{
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul li {
  list-style: none;
  color: #c3c3c3;
}

h1, h2, h3, h4, h5, i, b, em, div {
  font-weight: normal;
  font-style: normal;
  text-align: center;
  color: #c3c3c3;
}

i, b, em, span {
  display: inline-block;
  color: #c3c3c3;
}

a,a:hover,a:active,a:visited,a:link{
  text-decoration: none;
  cursor: pointer;
}

button {
  cursor: pointer;
  color: #c3c3c3;
}
input,button,select,textarea{
  outline:none;
  border: none;
}
input:focus {
  outline: none;
}

.displayflexcenter {
  display: flex;
  align-items: center;
  justify-content: center;
}

.displayflexbetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: wrap row;
}
.displayflexaround {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-flow: wrap row;
}
.displayflexstart {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-flow: wrap row;
}

.displayflexend {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-flow: wrap row;
}
.displayflexstart {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-flow: wrap row;
}
/*ant design the error message transform text to upcase*/
.ant-message span{
    text-transform: Capitalize !important;
}

